import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import Files from '@/components/Files'
import Chat from '@/components/Chat'

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home,
            children: [
                {
                    path: '',
                    component: Files
                },
                {
                    path: '/chat',
                    name: 'Chat',
                    component: Chat
                }
            ]
        }
    ]
})



