<template>
    <div class="chat">
        <div class="chat_area">
            <div class="text_area">
                <div class="message clear clearfix" v-for="(message, index) in messages" :key="index">
                    <span :class="{'left_side': message.type, 'right_side': !message.type}">{{message.content}}</span>
                    <i :class="{'left_side': message.type, 'right_side': !message.type}"></i>
                </div>
            </div>
            <textarea name="words" id="operation" cols="30" rows="10" v-model="textarea_value">adsdf</textarea>
            <span class="send" @click="send_message">send</span>
        </div>
        <div class="operation">

        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default {
        name: "Chat",
        data() {
            return {
                messages: [
                    {
                        type: 0,
                        content: 'hello world'
                    },
                    {
                        type: 1,
                        content: 'hello json'
                    },
                    {
                        type: 0,
                        content: 'hello worldhello worldhello worldhello worldhello worldhello worldhello worldhello worldhello worldhello world'
                    },
                    {
                        type: 1,
                        content: 'hello json'
                    }
                ],
                textarea_value: '',
                ws: ''
            }
        },
        methods: {
            send_message: function () {
                if (!this.textarea_value) {
                    console.log('error');
                    this.$Message.warning('发送内容不能为空');
                    return;
                }
                //this.ws.send(this.textarea_value);
                this.messages.push({
                    type: 0,
                    content: this.textarea_value
                });
                this.textarea_value = '';
            }
        },
        mounted: function () {
            if (!('WebSocket' in window)) {
                alert('您的浏览器不支持该功能');
            }
            /*Vue.http.get('http://127.0.0.1:3000/ws').then(res => {

                let ws = new WebSocket('ws://127.0.0.1:8001/ws');

                this.ws = ws;

                ws.onopen = function () {
                    ws.send('send message');
                };
                ws.onmessage = function (data) {
                    console.log(data);
                };
                ws.onerror = function (error) {
                    console.log(error);
                };
                ws.onclose = function () {
                    console.log('close');
                }
                
            }).catch(err => {
                console.log(err);
            })*/
        }
    }
</script>

<style scoped lang="less">
    .chat {
        font-size: 0;
        .chat_area, .operation {
            width: 49%;
            height: 500px;
            display: inline-block;
            box-sizing: border-box;
            border: 1px solid #000;
        }
        .chat_area {
            margin-right: 2%;
            .text_area {
                width: 100%;
                height: 450px;
                overflow: auto;
                font-size: 16px;
                .clear {
                    zoom: 1;
                    clear: both;
                }
                .clear:after {
                    content: '';
                    display: block;
                    clear: both;
                    visibility: hidden;
                    height: 0;
                }
                .message {
                    margin: 5px 10px;
                    position: relative;
                    span {
                        display: inline-block;
                        padding: 5px 10px;
                        background-color: #000;
                        opacity: .5;
                        border-radius: 5px;
                        color: #fff;
                        max-width: 90%;
                        &.left_side {
                            float: left;
                        }
                        &.right_side {
                            float: right;
                        }
                    }
                    i {
                        display: inline-block;
                        border: 5px solid;
                        position: absolute;
                        top: 12px;
                        opacity: .5;
                        &.left_side {
                            border-color: transparent #000 transparent transparent;
                            left: -10px;
                        }
                        &.right_side {
                            border-color: transparent transparent transparent #000;
                            right: -10px;
                        }
                    }
                }
            }
        }
        #operation {
            width: 88%;
            height: 48px;
            outline: none;
            resize: none;
            font-size: 12px;
            border-left: none;
            border-bottom: none;
            padding: 5px 10px;
        }
        .send {
            width: 12%;
            height: 48px;
            line-height: 48px;
            background-color: #ccc;
            font-size: 14px;
            display: inline-block;
            float: right;
            cursor: pointer;
            text-align: center;
            border-top: 1px solid #000;
            box-sizing: border-box;
        }
        .operation {
            vertical-align: top;
        }
    }
</style>


