<template>
    <div class="upload">
        <Upload
                multiple
                type="drag"
                action="/upload" :on-success="getFiles" :on-preview="preview">
            <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                <p>点击/拖拽文件到此处</p>
            </div>
        </Upload>
        <br>
        <h2>可下载列表</h2>
        <Input v-model="search_key">
            <Button slot="append" icon="ios-search" @click="search"></Button>
        </Input>
        <span class="download_list" v-for="(file, index) in files" :key="index" @click="downloadFile(file)">{{file}}</span>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default {
        name: "Files",
        methods: {
            preview: function (res) {
                console.log(res);
            },
            getFiles: function () {
                Vue.http.get('/files').then(res => {
                    console.log(res);
                    this.originFiles = res.body;

                    this.originFiles.splice(res.body.indexOf('.DS_Store'), 1);

                    this.files = this.originFiles.slice();
                }).catch(err => {
                    console.log(err);
                })
            },
            downloadFile: function (file) {
                /*Vue.http.get('http://127.0.0.1:3000/download?fileName=' + file).then(res => {
                    console.log(res);
                }).catch(err => {
                    console.log(err);
                })*/
                window.open('/download?fileName=' + file, '_self');
            },
            search: function () {
                let reg = new RegExp(this.search_key);
                let files = this.originFiles.slice();
                files = files.filter(item => {
                    return reg.test(item);
                });
                this.files = files;
            }
        },
        data() {
            return {
                files: [],
                search_key: '',
                originFiles: []
            }
        },
        mounted: function () {
            this.getFiles();
        }
    }
</script>

<style scoped lang="less">
    .upload {
        .download_list {
            display: inline-block;
            padding: 5px 10px;
            cursor: pointer;
            line-height: 30px;
            &:hover {
                background-color: #ccc;
                transition: background-color 300ms;
            }
        }
    }
</style>


